.App {
  text-align: center;
  margin: 20px;
  background-color: rgb(163, 204, 163);
  min-height: 900px;
}

textarea, input {
  width: 80%;
  margin: 10px 0 0 0;
  padding: 10px;
  border-radius : 5px;
}

button {
  background-color: blueviolet;
  border-color: purple;
  color : gold;
  margin: 0 10px 10px 10px;
  font-size: 20px;
}



pre {
  display: inline-block;
  width : 80%;
  padding: 10px;
  margin : 1px;
  background-color: #f4f4f4;
  border: 1px solid #ccc;

  text-align: left;

}


.copyBtn {

  background-color: chocolate;
  color : white;
  border-color : chocolate;
}



table {
  width : 80%;
  text-align: right;
  display: inline-table;
  
}


.tranBtn {
  float: left;

}

.memo {
  text-align: right;
  font-size: 10px;
  color:blue;
}

p {
  line-height: 0.2rem;
}
